import React from 'react';
import './App.css';
import Flat from './components/flat/flat';
import GoogleMap from './components/google-map/google-map';

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      flats: [],
      selectedFlat: null,
      searchItem: ''
    }
  }

  componentDidMount() {
    fetch('https://raw.githubusercontent.com/lewagon/flats-boilerplate/master/flats.json')
      .then(response => response.json())
      .then(flats => this.setState({ flats }))
      .catch(Error => console.log('Fetch Error:', Error));
  }

  selectFlat = (flat) => this.setState({ selectedFlat: flat });

  handleSearch = (e) => { this.setState({ searchItem: e.target.value }) }

  render() {
    const { flats, selectedFlat } = this.state;
    let center = {
      lat: 48.864716,
      lng: 2.349014
    }

    selectedFlat && (center = {
      lat: selectedFlat.lat,
      lng: selectedFlat.lng
    })

    return (
      <div className="app">
        <div className="main">
          <div className="search">
            <input type="text"
              placeholder="Search..."
              value={this.state.search}
              onChange={this.handleSearch} />
          </div>
          <div className="flats">
            {
              flats.filter(flat => flat.name.includes(this.state.searchItem)).map(flat => <Flat
                key={flat.id}
                flat={flat}
                handleClick={this.selectFlat}
                selected={flat === selectedFlat}
              />)
            }
          </div>
        </div>
        <div className="map">
          <GoogleMap flats={flats} selectedFlat={selectedFlat} center={center} />
        </div>
      </div>
    );
  }
}

export default App;