import React from 'react';
import './flat.css';

const Flat = ({ flat, handleClick, selected }) => {
  const title = `${flat.price} ${flat.priceCurrency} - ${flat.name}`
  const classes = 'flat' + (selected ? ' selected' : '')
  const style = {
    backgroundImage: `url('${flat.imageUrl}')`
  }
  return (
    <div onClick={() => handleClick(flat)} className={classes}>
      <div className="flat-picture" style={style}></div>
      <div className="flat-title">{title}</div>
    </div>
  );
}

export default Flat;